import { CircularProgress, styled } from "@rp/react/web/mui/core"

type Props = {
  fillBackground?: boolean
  loading?: boolean
}

const LoadSpinnerStyled = styled("div", {
  shouldForwardProp: prop => prop !== "fillBackground" && prop !== "loading",
})<Props>(({ fillBackground, loading, theme }) => {
  return {
    top: 0,
    left: 0,
    height: "100vh",
    width: "100%",
    marginLeft: 0,
    alignItems: "center",
    justifyContent: "center",
    zIndex: 1051,
    background: fillBackground ? theme.palette.background.default : "inherit",
    pointerEvents: fillBackground ? "auto" : "none",
    cursor: fillBackground ? "default" : "auto",
    display: loading ? "flex" : "none",
  }
})

const LoadingSpinner = ({ loading = true, fillBackground = false }: Props) => (
  <LoadSpinnerStyled
    data-testid="loading-spinner-container"
    loading={loading}
    fillBackground={fillBackground}
  >
    <CircularProgress color="primary" data-testid="loading-spinner" />
  </LoadSpinnerStyled>
)

export { LoadingSpinner }
